.header {
  position: sticky;
  background: white;
  top: 0;
  left: 0;
  z-index: 100;
}
@media screen and (max-width: 1440px) {
  .header .container-xxl .top-line {
    width: 100%;
  }
}
.header .navigation {
  padding: 16px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 991px) {
  .header .navigation {
    padding: 0px 6px;
  }
}
.header .burger-container {
  display: none;
}
@media screen and (max-width: 991px) {
  .header .burger-container {
    display: block;
  }
}
.header .burger-container .burger-menu {
  display: none;
}
@media screen and (max-width: 991px) {
  .header .burger-container .burger-menu {
    display: block;
    position: relative;
    height: 32px;
    width: 42px;
  }
  .header .burger-container .burger-menu:before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background: black;
  }
  .header .burger-container .burger-menu:after {
    content: "";
    position: absolute;
    display: block;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background: black;
  }
  .header .burger-container .burger-menu .burger-inner {
    content: "";
    position: absolute;
    display: block;
    top: 50%;
    left: 0;
    height: 2px;
    width: 100%;
    background: black;
  }
}
.header .burger-container .burger-nav {
  display: none;
  position: relative;
}
@media screen and (max-width: 991px) {
  .header .burger-container .burger-nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    top: 80px;
    left: -100%;
    width: 100%;
    height: 400px;
    transition: all 0.4s ease;
    background: #f5f6f6;
    z-index: 100;
  }
}
.header .burger-container .burger-nav .login-btn {
  padding: 10px 30px;
